@use "../../styles/typography";

.Container {
  display: flex;
  flex-direction: column;
  align-items: center;

  .TextContainer {
    font-size: #{typography.$size-default};
    padding: 0;
    margin-bottom: 40px;
    text-align: center;
  }

  .SubtextContainer {
    margin-bottom: 42px;
  }

  &.Left {
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .TextContainer {
      margin-bottom: 0;
      margin-left: 24px;
    }
  }
}
